.toggler__button {
  font-size: $font-size-16;
  line-height: 20px;
  font-weight: 400;
  background-color: transparent;
  border: none;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    font-size: $font-size-20;
    line-height: 30px;
    border-bottom: 1px dashed $color-gray-850;
    text-decoration: underline 1px dotted #666666;
    text-underline-offset: 5px;
  }
}

.toggler {
  padding: 0 35px;
  display: grid;
  grid-template-columns: 49px 1fr 49px;
  gap: 13px;
  align-items: center;

  @media (min-width: $tablet-width) {
    column-gap: 15px;
  }

  @media (min-width: $desktop-width) {
    column-gap: 25px;
  }
}

.toggler__range {
  border-radius: 6px;
  background-color: $white;
  border: 1px solid $gray-border;
  height: 14px;
  position: relative;

  &::after {
    content: "";
    position: absolute;
    top: 4px;
    bottom: 4px;
    left: 6px;
    height: 6px;
    background-color: #68B738;
    width: 36px;
    border-radius: 6px;
  }

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.toggler__input {
  appearance: none;
  background: transparent;
  height: 6px;


  &::-moz-range-thumb {
    width: 30px;
    height: 30px;
    background-color: $basic-green;
    appearance: none;
    border-radius: 50%;
    border: 11px solid $white;
  }

  &::-webkit-slider-thumb {
    appearance: none;
    width: 30px;
    height: 30px;
    background-color: $basic-green;
    border-radius: 50%;
    border: 11px solid $white;
    margin-top: -12px;
  }

  &::-webkit-slider-runnable-track {
    height: 6px;
    background-color: $gray-dark;
    border-radius: 6px;
  }

  &::-moz-range-track {
    height: 6px;
    background-color: $gray-dark;
    border-radius: 6px;
  }

  @media (min-width: $mobile-width-only) {
    display: none;
  }

  @media (min-width: $tablet-width) {
    display: block;
  }
}
